export default {
  data() {
    return {
      form: {
        experience: "",
        help: "",
        task_txt: ""
      },
      rules: {
        experience: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        help: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        task_txt: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ]
      },
      keys: "",
      id: "",
      allData: {},
      loading: false
    };
  },
  created() {
    this.initData();
  },
  methods: {
    send(formName, type) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          if (type === 1) {
            // console.log(this.form,this.allData);

            let { data } = await this.$http.HallFunConfig.PutActivityNew3({
              keys: this.keys,
              values: JSON.stringify(this.allData),
              id: this.id
            });
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.$message({
                type: "success",
                message: "保存成功!"
              });
            }else{
               this.$message({
                type: "warning",
                message: "保存失败!"
              });
            }
          } else if (type === 2) {
            this.loading = true;
            let { data } = await this.$http.HallFunConfig.PostActivityNew3({
              keys: this.keys,
              values: JSON.stringify(this.allData),
              id: this.id
            });
            // console.log(data);
            if (data.code === 1 && data.msg === "ok") {
              this.loading = false;
              this.$message({
                type: "success",
                message: "发送服务器配置成功!"
              });
            } else {
              this.loading = false;
              this.$message({
                type: "warning",
                message: "发送服务器配置失败"
              });
            }
          }
        } else {
          this.$message({
            type: "warning",
            message: "必填的项不可以为空"
          });
          return false;
        }
      });
    },

    async initData() {
      let { data } = await this.$http.HallFunConfig.GetActivityNew3({
        key: "activity_new.lua"
      });
      //   console.log(data);
      this.keys = data.data[0].sys_key;
      this.id = data.data[0].id;
      let res = data.data[0].sys_val;
      this.allData = JSON.parse(res);
      Object.keys(this.allData).forEach(item => {
        if (this.allData[item].ac_type === "10003") {
          this.form = this.allData[item].ac_content.other[0];
        }
      });
      // console.log(this.form, this.allData);
    }
  }
};



